body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: Lato-Regular;
}

input {
  font-family: Lato-Regular;
}

@font-face {
  font-family: Lato-Bold;
  src: url(/static/media/Lato-Black.e798200a.ttf);
  font-style: normal;
}

@font-face {
  font-family: Lato-Regular;
  src: url(/static/media/Lato-Regular.fa62c04b.ttf);
  font-style: normal;
}

@font-face {
  font-family: Monsterrat-Regular;
  src: url(/static/media/Montserrat-Regular.9c460951.ttf);
  font-style: normal;
}

@font-face {
  font-family: 'montserratmedium';
  src: url(/static/media/Montserrat-Medium.a98626e1.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
